@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,700;1,600&display=swap);
.landing {
  background: url("https://images.unsplash.com/photo-1585955341113-27c13e8815d6?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1558&q=80");
  background-size: cover;
}

.landing-background {
  background: linear-gradient(
    var(--main-transparent-color),
    var(--main-transparent-color)
  );
  background-size: cover;
  color: var(--text-white);
  text-shadow: 0px 0px 8px rgba(0, 0, 0, 0.55);
}

.about {
  background-size: cover;
}

.about-background {
  background: linear-gradient(
      var(--main-transparent-color),
      var(--main-transparent-color)
    ),
    url("https://images.unsplash.com/photo-1501769214405-5e5ee5125a02?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1396&q=80");
  background-size: cover;
  color: var(--text-white);
  padding: 0 50px;
  text-shadow: 0px 0px 8px rgba(0, 0, 0, 0.55);
}

.skills {
  background: linear-gradient(
      var(--background-lighter-color-transparent),
      var(--background-lighter-color-transparent)
    ),
    url("https://images.unsplash.com/photo-1563191911-778cc72ad7dd?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1500&q=80");
  background-attachment: fixed;
  background-size: cover;
  color: var(--text-white);
}

.progress-card {
  background: var(--background-lighter-color);
}

.progress-bar {
  transition-delay: 0.5s !important;
  transition-duration: 1s !important;
}

.skills-background {
  color: var(--text-white);
  text-shadow: 0px 0px 8px rgba(0, 0, 0, 0.55);
}

.contact {
  background: url("https://images.unsplash.com/photo-1517816743773-6e0fd518b4a6?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1500&q=80");
  background-size: cover;
  width: 100vw !important;
}

.contact-background {
  background: linear-gradient(
    var(--main-transparent-color),
    var(--main-transparent-color)
  );
  background-size: cover;
  color: var(--text-white);
  padding: 0 50px;
  text-shadow: 0px 0px 8px rgba(0, 0, 0, 0.55);
}

.text-shadow {
  text-shadow: 0px 0px 8px rgba(0, 0, 0, 0.3);
}

.divider-y {
  background: var(--background-color);
  position: relative;
  min-width: 1px !important;
  padding: 0 5px;
  height: 100vh;
  margin: 0;
  z-index: 999;
}

.divider-x {
  margin: 0;
  width: 100vw !important;
  padding: 5px 0;
  background: var(--background-color);
  position: relative;
  min-height: 1px !important;
  z-index: 999;
}

:root {
  --main-transparent-color: rgba(17, 138, 178, 0.8);
  --main-solid-color: rgb(17, 138, 178);
  --background-color: #333;
  --background-lighter-color: #3f3f3f;
  --background-lighter-color-transparent: rgba(31, 31, 31, 0.95);
  --text-white: #fafafa;
}

html {
  overflow-x: hidden;
  font-family: "Montserrat", sans-serif !important;
}

.full-w {
  width: 100vw !important;
}

.full-h {
  height: 100vh !important;
}

section {
  scroll-snap-align: start;
}

